* {
  margin: 0;
  padding: 0;
  line-height: 1.5;
}
/* 변수로 색상을 지정  */

:root {
  --baseColor: #fff;
  /* 주조색(배경색) - 약 70% 사용 */
  --dominantColor: black;
  /* 보조색 - 20% 사용 */
  --accentColor: royalblue;
}
h1 {
  color: var(--accentColor);
  background-color: var(--dominantColor);
  line-height: 2;
  text-align: center;
}
section {
  margin-top: 3%;
}
h2 {
  text-align: center;
  font: 1em;
  height: 2em;
  color: var(--accentColor);
}
article {
  padding: 1em;
}
h3 {
  margin-bottom: 1em;
}
article ul,
article ol {
  padding-left: 2em;
}

.overview {
  padding: 0.5em;
}
h4 {
  height: 2em;
}
.tip {
  padding: 0.5em 1em;
}
h5 {
  font-size: 1em;
}
table {
  border-collapse: collapse;
  border-spacing: none;
  width: 100%;
}
caption {
  text-align: left;
  height: 2em;
}
th,
td {
  border: 0.1em solid var(--dominantColor);
  padding: 0.5em;
}
tbody th {
  text-align: left;
  background-color: #ddd;
}
.del {
  color: #ddd;
}
.point {
  font-weight: bold;
  color: var(--accentColor);
}
